body {
	background-color: #282c34;
	color: white;
}

.header {
	background: white;
	padding: 5px 0 5px 7px;
	font-size: 24px;
	display: flex;
	align-items: center;
}

.content {
	padding-left: 16px;
}

a {
	text-decoration: inherit;
	color: #8a8ecb;
}

a:hover {
	text-decoration: underline;
}

.App-logo {
	height: 48px;
}

.notFound {
	display: "flex";
	align-items: "center";
	justify-content: "center";
	font-size: 64px;
	position: "absolute";
	width: "100%";
	height: "100%";
}
